.button-reserv {
  width: 434px;
  height: 64px;
  font-size: 24px;
  line-height: 1.2;
  border-radius: 25px;
}

.button-modal {
  width: 592px;
  height: 64px;
  font-size: 24px;
  line-height: 1.2;
  border-radius: 2px;
}

.button-modal-mob-oval {
  width: 384px;
  height: 64px;
  border-radius: 25px;

  font-size: 20px;
  line-height: 1.2;
}

.button-modal-mob-rectangle {
  width: 384px;
  height: 64px;
  border-radius: 2px;

  font-size: 20px;
  line-height: 1.2;
}

.button-reserv,
.button-modal,
.button-modal-mob-oval,
.button-modal-mob-rectangle {
  background: $black;
  border: 1px solid $accent-color;

  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 500;

  color: $white;

  transition: background-color $main-transition-duration linear,
    color $main-transition-duration linear;
  transition-timing-function: $main-transtition-timing-function;

  &:hover,
  &:focus {
    background: $accent-color;
    color: $black;
  }
}

.button-gray {
  background: rgba(0, 0, 0, 0.4);
}

.button-black {
  background: $black;
  width: 236px;
  height: 48px;
}

.button-gray,
.button-black {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  @include font(16px, 700, 0.8, 0.02em);
  color: $accent-color;

  border: 2px solid $white;
  border-radius: 25px;

  // width: 236px;
  // height: 48px;

  transition: background-color $main-transition-duration linear,
    color $main-transition-duration linear;
  transition-timing-function: $main-transtition-timing-function;

  &:hover,
  &:focus {
    background: $white;
    border: 2px solid $white;
  }
}
